import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import CloseIcon from '@material-ui/icons/Close';
import { IconButton } from '@material-ui/core';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import { useHistory } from 'react-router-dom';

const theme = createMuiTheme({
  palette: {
    type: 'dark',
  },
});


const useStyles = makeStyles((theme) => ({
  closeButton: {
    position: 'absolute',
    top: '5px',
    right: '8px',
    zIndex: 1300,
  },
  closeButtonSmallScreen: {
    position: 'absolute',
    top: '0px',
    right: '3px',
    zIndex: 1300,
  },
  overlay: {
    backgroundColor: 'rgba(31, 31, 31, 0.4)',
    position: 'fixed',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    touchAction: 'none',
    userSelect: 'none',
  },
  modal: {
    display: 'flex',
    padding: theme.spacing(1),
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  myPaper: {
    width: 'calc(100% - 96px)'
  },
}));

function GigaradModalPage(props) {
  const classes = useStyles();
  const matches = useMediaQuery('(min-width:975px)');
  const rootRef = React.useRef(null);
  const history = useHistory();

  return(
    <ThemeProvider theme={theme}>
      <div className={classes.overlay} ref={rootRef}>
        <Dialog
          disablePortal
        open
        scroll="paper"
        aria-labelledby="scroll-dialog-title"
        aria-describedby="scroll-dialog-description"
        fullWidth
        maxWidth="md"
        classes={{paperFullWidth: classes.myPaper}}
      >
        <DialogContent>
          {props.children}
        </DialogContent>
      </Dialog>
        <IconButton
          onClick={(e) => {
            e.stopPropagation();
            console.log(history.length);
            if (history.length > 2) {
            history.goBack();
          } else {
            history.push('/');
          }
          }}
          color="inherit"
          className={matches ? classes.closeButton : classes.closeButtonSmallScreen}
        >
          <CloseIcon fontSize="large" />
        </IconButton>
      </div>
    </ThemeProvider>
  )
}

GigaradModalPage.propTypes = {
};

GigaradModalPage.defaultProps = {
};

export default GigaradModalPage;
